<template>
    <button v-if="isLogged" class="btn btn-primary" :disabled="disabled" :config="config" @click="toggleBuddy">
        <font-awesome-icon :icon="icon" /> {{ text }}
    </button>
</template>

<script>
import User from '@api/user';
import GenericMixin from '@mixins/generic';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

export default {
    name: 'relationship-button',

    mixins: [GenericMixin],

    components: {
        FontAwesomeIcon
    },

    computed: {
        icon () {
            if (this.areBuddies) {
                return 'user-times';
            } else {
                return 'user-plus';
            }
        },
        text () {
            if (this.areBuddies) {
                return this.getTrans('remove');
            } else {
                return this.getTrans('add');
            }
        },
    },

    methods: {
        toggleBuddy () {
            this.disabled = true;

            let promise;
            if (this.areBuddies) {
                promise = User.removeBuddy(this.user.id);
            } else {
                promise = User.addBuddy(this.user.id);
            }

            const that = this;
            promise.then(() => {
                that.areBuddies = !that.areBuddies;
                that.disabled = false;
            }).catch((e) => {
                console.error('An error occured:', e.message);
                that.disabled = false;
            });
        },
    },

    data () {
        return {
            areBuddies: this.config.areBuddies,
            user: this.config.userProfile,
            disabled: false,
        };
    }
};
</script>

<style lang="scss" scoped>
</style>



